import { LoginEntrenaBasics } from './../../../../Interfaces/loginEntrena.interface';
import { Component, OnInit, Input } from '@angular/core';
import { ApiEntrenaService } from 'src/app/Services/Api/ApiEntrena.service';
import { ToastService } from 'src/app/Services/Utilities/Toast.service';
import { ModalController } from '@ionic/angular';
import { trigger, transition, animate, style } from '@angular/animations';
import { StorageWebService } from 'src/app/Services/Storage/StorageWeb.service';

@Component({
  selector: 'app-users',
  templateUrl: './users.page.html',
  styleUrls: ['./users.page.scss'],
  animations: [
    trigger('entryMessage', [
      transition(':enter', [
        style({
          opacity: 0,
          transform: 'scale(0)'
        }),
        animate('200ms', style({
          opacity: 1,
          transform: 'scale(1)'
        }))
      ]),

      transition(':leave', [
        style({
          opacity: 1,
          transform: 'scale(1)'
        }),
        animate('200ms', style({
          opacity: 0,
          transform: 'scale(0)'
        }))
      ])
    ])
  ]
})
export class UsersEntrenaModalPage implements OnInit {
  existUser = false;
  createdAction = false;
  equalPassword = true;

  userTemp = '';

  login: LoginEntrenaBasics = {
    login: '',
    password: '',
    confirPassword: '',
    name: '',
    time: 1,
    id: ''
  };

  @Input() action;
  @Input() data;

  constructor(
    private apiEntrena: ApiEntrenaService,
    private toasCtrl: ToastService,
    private modalCtrl: ModalController,
    private storage: StorageWebService
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter(){
   if ( this.action === 'edit') {
      this.login = this.data;
      this.userTemp = this.data.login;
   }
  }

  validateUser( event ) {
    if (this.action === 'edit') {
      if (event.detail.value !== this.userTemp) {
        this.funcValidateUser(event.detail.value);
      }
    } else {
      this.funcValidateUser(event.detail.value);
    }
  }

  funcValidateUser( value ) {
    this.apiEntrena.existUser( {login: value }).subscribe((dataExist: any) => {

      if (dataExist.status === 'ok') {
          if (dataExist.response.exist) {
            this.existUser = true;
          } else {
            this.existUser = false;
          }
      } else {
          this.toasCtrl.newCreatedToast('No se pudo consultar el usuario, intentelo mas tarde', false);
          this.modalCtrl.dismiss({});
      }
  });
  }


  validatePassword(event, passwordcamp) {
     if (this.login.password !== this.login.confirPassword) {
       this.equalPassword = false;
     } else {
       this.equalPassword = true;
     }
  }

  async execute() {
    if (this.equalPassword && !this.existUser && this.login.login !== '' && this.login.name !== '' && this.login.time >= 1) {
        const login = await this.storage.getLogin();

        if (login) {
          if (this.action === 'edit') {
            this.apiEntrena.modifyUserBasic(this.login, login[0].token, this.login.id).subscribe((data: any) => {
              console.log(data);
              if (data.status === 'ok') {
                this.toasCtrl.newCreatedToast('Usuario editado correctamente', true);
              } else {
                this.toasCtrl.newCreatedToast('No se pudo editar el usuario, intentelo mas tarde', false);
              }
              this.modalCtrl.dismiss({reload: true});
            }, (errpr) => {
              this.toasCtrl.newCreatedToast('No se pudo editar el usuario, intentelo mas tarde', false);
              this.modalCtrl.dismiss({reload: true});
            });
          } else {
            this.apiEntrena.createdUserBasic(this.login, login[0].token).subscribe((data: any) => {
              console.log(data);
              if (data.status === 'ok') {
                this.toasCtrl.newCreatedToast('Usuario creado correctamente', true);
              } else {
                this.toasCtrl.newCreatedToast('No se pudo consultar el usuario, intentelo mas tarde', false);
              }
              this.modalCtrl.dismiss({reload: true});
            }, (errpr) => {
              this.toasCtrl.newCreatedToast('No se pudo consultar el usuario, intentelo mas tarde', false);
              this.modalCtrl.dismiss({reload: true});
            });
          }
        }
    } else {
      this.toasCtrl.newCreatedToast('Debe de llenar todos los campos correctamente', false);
    }
  }

  closeWindow() {
    this.modalCtrl.dismiss({});
  }

}
