import { ModalAssentsComponent } from './../../../components/modal-assents/modal-assents.component';
import { ModalUsersComponent } from './../../../components/modal-users/modal-users.component';
import { LoginEntrenaBasics } from './../../../Interfaces/loginEntrena.interface';
import { UsersEntrenaModalPage } from './../Modals/users/users.page';
import { Component, OnInit } from '@angular/core';
import { ModalController, AlertController } from '@ionic/angular';
import { ApiEntrenaService } from 'src/app/Services/Api/ApiEntrena.service';
import { StorageWebService } from 'src/app/Services/Storage/StorageWeb.service';
import { ToastService } from 'src/app/Services/Utilities/Toast.service';
import { Socket } from 'ngx-socket-io';
import { DataComparnyService } from 'src/app/Services/dataCompany.service';

@Component({
  selector: 'app-admin-users',
  templateUrl: './admin-users.page.html',
  styleUrls: ['./admin-users.page.scss'],
})
export class AdminUsersPage implements OnInit {

  listUsersBasic = [];
  text = '';
  color = '';

  constructor(
    private modalCtrl: ModalController,
    private apiEntrena: ApiEntrenaService,
    private storage: StorageWebService,
    private toastCrtrl: ToastService,
    private alertCtrl: AlertController,
    private socket: Socket,
    private dataCompany: DataComparnyService
  ) { 
  
    this.dataCompany.$color.subscribe((color) => {
        this.color = color;
    })

  }

  ngOnInit() {
  }

  ionViewWillEnter() {
      this.listUsers();
  }

  async listUsers() {
    const login = await this.storage.getLogin();

    if (login) {
      this.apiEntrena.getUsersBasic(login[0].token).subscribe((data: any) => {
        console.log(data);
        if (data.status === 'ok') {
            this.listUsersBasic = data.response;
        } else {
            this.toastCrtrl.newCreatedToast('No se pudo listar los usuarios, intentelo mas tarde', false);
        }
      }, (error) => {
        this.toastCrtrl.newCreatedToast('No se pudo listar los usuarios, intentelo mas tarde', false);
      })
    }
  }


  async modalUser(actionUser: any, datauser: any) {

    const modal = await this.modalCtrl.create({
      component: UsersEntrenaModalPage,
      componentProps: {
        action: actionUser,
        data: datauser
      },
      keyboardClose: true
    });

    await modal.present();

    const closeModal = await modal.onWillDismiss();

    console.log(closeModal)

    if (closeModal) {

      if (closeModal.data) {
        this.listUsers();
      }
      
    }
  }

  editUser( userBasic ) {
    const data: LoginEntrenaBasics = {
      login: userBasic.login,
      password: userBasic.password,
      confirPassword: userBasic.password,
      name: userBasic.name,
      time: userBasic.configuration.timeLimit,
      id: userBasic._id
    };
    this.modalUser('edit', data);
  }

  async deleteUser(userBasic) {
    const alert = await this.alertCtrl.create({
      header: 'Esta seguro de eliminar este usuario ?',
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel'
        },
        {
          text: 'Aceptar',
          handler: async () => {
            const login = await this.storage.getLogin();

            if (login) {
              this.apiEntrena.deleteUserBasic(login[0].token, userBasic._id).subscribe((data: any) => {
                if (data.status === 'ok') {
                  this.toastCrtrl.newCreatedToast('Usuario eliminado de la lista', true);
                  this.listUsers();
                } else {
                  this.toastCrtrl.newCreatedToast('No se pudo eliminar el usuario, intentelo mas tarde', false);
                }
              }, (error) => {
                this.toastCrtrl.newCreatedToast('No se pudo eliminar el usuario, intentelo mas tarde', false);
              })
            }
          }
        }
      ]
    })

    await alert.present();
  }

  async add(user) {
    const modal = await this.modalCtrl.create({
      component: ModalUsersComponent,
      componentProps: {
        user: user._id
      }
    })

    await modal.present();
  }

  async history(user) {
    const modal = await this.modalCtrl.create({
      component: ModalAssentsComponent,
      componentProps: {
        user: user._id
      }
    })

    await modal.present();
  }

  async statusUser( event, user) {
    const login = await this.storage.getLogin();

    if ( login ) {
      // tslint:disable-next-line:max-line-length
      this.apiEntrena.statusUser({id: user._id, status: event.detail.checked, timeLimit: user.configuration.timeLimit}, login[0].token).subscribe((data: any) => {
        console.log(data);
        if (data.status === 'ok') {
            if (!event.detail.checked) {
                this.socket.emit('changeStatusUser', {
                  _id: user._id 
                });
            }
            this.toastCrtrl.newCreatedToast('User status changed successfully', true);
        } else {
          this.toastCrtrl.newCreatedToast('Could not change user status', false);
        }
      }, (err) => {
        this.toastCrtrl.newCreatedToast('Could not change user status', false);
      })
    }
  }

  changeText( event ) {
    this.text = event.detail.value;
  }

}
